// Components
.hero-container,
.hero-image {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    padding: 1rem;
    max-width: 440px;
    width: 100%;
}

.hero-container {
    z-index: 1;
}

.hero-image {
    position: relative;
    pointer-events: none;
    min-height: 320px;

    img {
        width: 1125px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        height: auto;

        @media (max-width: 768px) {
            transform: translateX(-55%) translateY(-65%);
        }
    }
}

.logo-section {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: flex-start;

    img {
        height: 6rem;
        width: auto;
        border-radius: 1.5rem;
        box-shadow: 
            0px 10px 15px -3px rgba(0, 0, 0, 0.10), 
            0px 4px 6px -4px rgba(0, 0, 0, 0.10);
    }
}

.action-section {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: flex-start;
    
}

.action-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(0.5rem);
    -webkit-backdrop-filter: blur(0.5rem);
    border: 1px solid white;
    color: var(--slate-900);
    padding: 0.75rem 1.5rem;
    gap: 0.25rem;
    height: 4rem;
    border-radius: 2rem;
    font-size: 1.25rem;
    line-height: 1.25rem;
    box-shadow: 
        0px 1px 3px 0px rgba(0, 0, 0, 0.10), 
        0px 1px 2px -1px rgba(0, 0, 0, 0.10);

    &:hover,
    &:focus {
        background-color: white;
        color: var(--sky-950);
        transform: translateY(-0.125rem);
        box-shadow: 
        0px 2px 16px 0px var(--amber-500), 
        0px 2px 24px -2px var(--amber-500);
    }

    &:active {
        transform: translateY(0);
        box-shadow: 
        0px 1px 3px 0px rgba(0, 0, 0, 0.10), 
        0px 1px 2px -1px rgba(0, 0, 0, 0.10);
    }

    @media (max-width: 768px) {
        width: 100%;
    }

    img {
        width: 1.5rem;
        height: 1.5rem;
        object-fit: cover;
    }
}

.qr-section {
    display: flex;
    align-items: center;
    gap: 1.5rem;

    @media (max-width: 768px) {
        display: none;
    }

    h2 {
        display: flex;
        flex: 1;
    }
    
    .qr-container {
        display: flex;
        width: 10rem;
        height: 10rem;
        border-radius: 0.75rem;
        background-color: white;
        padding: 1.5rem;
        box-shadow: 
            0px 10px 15px -3px rgba(0, 0, 0, 0.10), 
            0px 4px 6px -4px rgba(0, 0, 0, 0.10);
        transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
        
        &:hover {
            transform: scale(1.2);
        }
        
        img {
            width: 100%;
            height: auto;
        }
    }
}